:root {
  color-scheme: dark light;
  --canvas: light-dark(var(--color-white), var(--bg-page));
  --ink: light-dark(var(--bg-page), var(--color-white));
}

@font-face {
  font-family: "Clash Display";
  src: url("../fonts/ClashDisplay/ClashDisplay-Variable.woff2") format("woff2"), url("../fonts/ClashDisplay/ClashDisplay-Variable.woff") format("woff");
  font-weight: 200 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Satoshi";
  src: url("../fonts/Satoshi/Satoshi-Variable.woff2") format("woff2"), url("../fonts/Satoshi/Satoshi-Variable.woff") format("woff");
  font-weight: 300 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Satoshi";
  src: url("../fonts/Satoshi/Satoshi-VariableItalic.woff2") format("woff2"), url("../fonts/Satoshi/Satoshi-VariableItalic.woff") format("woff");
  font-weight: 300 900;
  font-style: italic;
  font-display: swap;
}
:root {
  --font-family-heading: 'Clash Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  --font-family-body: 'Satoshi', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  --font-family-mono: 'Fira Code', 'Cascadia Code', 'Consolas', 'Monaco', 'Courier New', monospace;
}

:root {
  --font-size-xs: 0.75rem;
  --font-size-sm: 0.875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --font-size-2xl: 1.5rem;
  --font-size-3xl: 2.5rem;
  --font-size-4xl: 3rem;
  --font-size-5xl: 3.625rem;
  --font-size-6xl: 4rem;
}

:root {
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-black: 900;
}

:root {
  --line-height-tight: 1.25;
  --line-height-normal: 1.5;
  --line-height-relaxed: 1.75;
  --line-height-loose: 2;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-family-heading);
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-tight);
  margin-top: 0;
  margin-bottom: 0.5em;
  color: var(--base-color);
}

h1 {
  font-size: var(--font-size-5xl);
  font-weight: var(--font-weight-bold);
}

h2 {
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-semibold);
}

h3 {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-semibold);
}

h4 {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-medium);
}

h5 {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-medium);
}

h6 {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-medium);
}

body {
  font-family: var(--font-family-body);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-regular);
  line-height: var(--line-height-normal);
}

p {
  margin-top: 0;
  margin-bottom: 1em;
}

strong, b {
  font-weight: var(--font-weight-bold);
}

em, i {
  font-style: italic;
}

footer {
  grid-row: footer;
}

body > .layout {
  align-items: center;
  display: grid;
  column-gap: var(--spacing-inline);
  row-gap: var(--spacing-block);
  grid-auto-columns: 1fr;
  grid-template-columns: [full-width] auto;
  grid-template-rows: [header-start] auto [header-end main-start] 1fr [main-end footer-start] auto [footer-end];
  justify-items: stretch;
  max-width: 100svw;
  min-height: 100vh;
}

.content-grid {
  display: grid;
  column-gap: var(--spacing-inline);
  row-gap: var(--spacing-block);
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  grid-template-columns: [full-width-start breakout-start] var(--size-fluid-4) [aside-primary-start] 1fr [aside-primary-end content-start] 2fr [content-end aside-secondary-start] 1fr [aside-secondary-end] var(--size-fluid-4) [breakout-end full-width-end];
  padding-block: var(--spacing-block);
  padding-inline: var(--spacing-inline);
  width: clamp(300px, 75svw, var(--content-max-width));
}
.content-grid > * {
  grid-column: content;
}

header[role=banner] {
  grid-column: full-width;
  grid-row: header;
}

main {
  grid-row: main;
}

section {
  display: grid;
}

#hero {
  display: grid;
  height: 66vh;
  place-items: center;
  position: relative;
}

#hero::before {
  aspect-ratio: 16/9;
  background: #5b2c91;
  border-radius: 50%;
  box-shadow: 0 0 60px 30px rgba(200, 150, 255, 0.8), 0 0 100px 50px rgba(180, 130, 255, 0.4);
  content: "";
  display: block;
  left: 50%;
  max-width: 120vw;
  position: absolute;
  top: 0;
  transform: translate(-50%, -50%); /* Less negative = lower */
  width: 120vw;
  z-index: 0;
}

#hero .container {
  position: relative;
  z-index: 1;
}

.stars-wrapper {
  background: var(--base-200);
  clip-path: ellipse(60vw 40vw at 50% 20vw); /* Larger vertical radius and lower position */
  height: 100%;
  inset: 0;
  overflow: hidden;
  perspective: 340px;
  position: absolute;
  width: 100%;
  z-index: 0;
}

